import { prisma } from "@/lib/prisma";

type Params = { slug: string };

export default async function ArtisanPage({ params }: { params: Promise<Params> }) {
  const { slug } = await params;

  const profile = await prisma.artisanProfile.findUnique({ where: { slug } });
  if (!profile) return <main className="p-6">Artisan not found.</main>;

  const products = await prisma.product.findMany({
    where: { artisanId: profile.id },
    orderBy: { createdAt: "desc" },
  });

  return (
    <main className="mx-auto max-w-6xl p-6">
      {/* your existing cover/avatar/about */}

      <section className="mt-8">
        <h2 className="text-xl font-bold mb-3">Products</h2>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          {products.map(p => (
            <a key={p.id} href={`/products/${p.id}`} className="border rounded p-3">
              <img src={p.imageUrl || "/placeholder.jpg"} alt={p.name} className="w-full h-56 object-cover rounded" />
              <div className="mt-2 font-semibold">{p.name}</div>
              <div className="text-slate-600">â‚±{(p.priceCents / 100).toFixed(2)}</div>
            </a>
          ))}
          {products.length === 0 && <p className="text-slate-500">No products yet.</p>}
        </div>
      </section>
    </main>
  );
}